.quote-container {
  grid-area: quote;
  width: 80%;
  display: -webkit-flex;
  display: flex;
  justify-self: center; }

.quote {
  text-align: center;
  margin: auto; }

.flourish {
  -webkit-align-self: center;
          align-self: center;
  width: 45rem; }

.flourish-left {
  -webkit-transform: rotate(-90deg);
          transform: rotate(-90deg); }

.flourish-right {
  -webkit-transform: rotate(-90deg) scaleY(-1);
          transform: rotate(-90deg) scaleY(-1); }

.flourish-container-right {
  grid-area: flor-right;
  display: inline-grid;
  -webkit-justify-content: end;
          justify-content: end; }

.flourish-container-left {
  align-self: center;
  grid-area: flor-left; }

.app-container {
  display: grid;
  grid-template-areas: 'flor-left quote flor-right' 'button button button';
  margin: auto;
  height: 97vh;
  grid-auto-columns: 20% auto 20%;
  grid-template-rows: auto 3em; }

.button {
  position: relative;
  vertical-align: top;
  font-family: 'Sue Ellen Francisco', cursive;
  font-size: 2rem;
  width: 100%;
  height: 60px;
  padding: 0 2.5rem;
  font-size: 2rem;
  outline: none;
  color: white;
  text-align: center;
  text-shadow: 0 1px 2px rgba(0, 0, 0, 0.25);
  background: #9b59b6;
  border: 0;
  border-bottom: 2px solid #8d4ca7;
  cursor: pointer;
  box-shadow: inset 0 -2px #8d4ca7; }

.button:active {
  top: 1px;
  outline: none;
  box-shadow: none; }

.button-container {
  grid-area: button;
  justify-self: center; }

